import { keyframes } from "@mui/material/styles"
import { Checkbox, TextField, styled } from "@rp/react/web/mui/core"
import { Search } from "@rp/react/web/mui/icons"
import { getPixelSize } from "./rpJsonToolbarUtils"

const SearchIcon = styled(Search)(({ theme }) => ({
  color: theme.palette.grey[400],
  height: "18px",
  fontSize: "16px",
  marginRight: "6px",
}))

const keyframe = keyframes`
    from {
      width: 0;
      opacity: 0;
      to {
      width: 100%;
      opacity: 1;
    }
  }
  `
const GrowingSearch = styled(TextField, {
  name: "GrowingSearchBar",
  shouldForwardProp: props => props !== "visible",
})(({ visible }: { visible: boolean }) => ({
  // animation in
  animation: `${keyframe} 0.4s ease-in-out`,
  float: "right",
  // animation out
  width: visible ? "100%" : 30,
  transition: "all 0.3s ease-in-out",
}))

export type CheckboxProps = {
  state?: number
  customStyle?: Array<Record<string, React.CSSProperties> | undefined>
  size?: "small" | "medium" | undefined
}

const CheckboxStyled = styled(Checkbox, {
  name: "MarkQueryEntity",
  shouldForwardProp: prop => prop !== "customStyle",
})<CheckboxProps>(({ customStyle, size, theme, state = 0 }) => {
  const pixelSize = getPixelSize(size)
  const { root, hover } = customStyle?.[state] || {}
  return {
    "&.MuiCheckbox-root": {
      background: theme.palette.grey[900],
      borderRadius: "4px",
      color: theme.palette.grey[400],
      border: `1px solid ${theme.palette.grey[400]}`,
    },
    "&.MuiCheckbox-root:hover": {
      color: theme.palette.grey[100],
      background: theme.palette.grey[500],
      border: `1px solid  ${theme.palette.grey[900]}`,
      ...hover,
    },
    "&.MuiCheckbox-root.Mui-checked": {
      color: theme.palette.grey[100],
      background: theme.palette.grey[500],
      border: `1px solid ${theme.palette.grey[900]}`,
      ...root,
    },
    "&.MuiCheckbox-root.Mui-checked:hover": {
      backgroundColor: theme.palette.grey[500],
      color: theme.palette.grey[100],
      ...hover,
    },
    "&.MuiBadge-anchorOriginTopRight": {
      right: 4,
    },
    width: pixelSize,
    height: pixelSize,
  }
})

export { SearchIcon, GrowingSearch, CheckboxStyled }
